import { useContext, useEffect, useState } from 'react';
import s from './PatientInfo.module.scss';
import cn from 'classnames'
import { UserIdContext } from '../../../context/context';

function PatientInfo({ patientName, setPatientName, patientPhone, setPatientPhone, handleDataInput }) {
  const [isNameValid, setIsNameValid] = useState(true);
  const [isPhoneValid, setIsPhoneValid] = useState(true);

  const userId = useContext(UserIdContext);

  const validateName = () => {
    setIsNameValid(patientName.trim() !== '');
  };

  const validatePhone = () => {
    const phoneRegex = /^380\d{9}$/;
    setIsPhoneValid(phoneRegex.test(patientPhone));
  };

  useEffect(() => {
    validateName();
    validatePhone();
    handleDataInput();
  }, [patientName, patientPhone, handleDataInput]);

  const handleChangeName = (e) => {
    setPatientName(e.target.value);
  }

  const handleChangePhone = (e) => {
    setPatientPhone(e.target.value);
  }

  let blockClass = cn(s.info__block, {
    [s.browser]: !userId,
  });

  return (
    <div className={s.info}>
      <h1 tabIndex={0} className={s.info__title}>–ó–∞–ø–∏—Å</h1>
      <div className={blockClass}>
        <label>
          <span>–ü–Ü–ë:</span>
          <input
            placeholder="–ü–Ü–ë"
            type="text"
            value={patientName}
            onChange={handleChangeName}
            onBlur={validateName}
            required
          />
          {!isNameValid && <p tabIndex={0}>–ü–æ–ª–µ –æ–±–æ–≤'—è–∑–∫–æ–≤–µ –¥–æ –∑–∞–ø–æ–≤–Ω–µ–Ω–Ω—è</p>}
        </label>
      </div>
      <div className={blockClass}>
        <label>
          <span>–ù–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω—É:</span>
          <input
            placeholder="–ù–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω—É"
            type="tel"
            value={patientPhone}
            onChange={handleChangePhone}
            onBlur={validatePhone} 
            required
          />
          {!isPhoneValid && <p tabIndex={0}>–§–æ—Ä–º–∞—Ç –ø–æ–≤–∏–Ω–µ–Ω –±—É—Ç–∏ 380 –•–• –•–•–•–•–•–•–•</p>}
        </label>
      </div>
    </div>
  );
}

export default PatientInfo;

